import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MarkSixResult } from '../models/common.model';
import { Mark6Service } from '../services/mark6.service';
import { CommonService } from '../services/common.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit, OnDestroy {

  result: MarkSixResult;
  loaded: boolean;

  constructor(private m6service: Mark6Service, private commonService: CommonService,
    private _router: Router, private _activeRoute: ActivatedRoute) { }

  ngOnInit() {
    this.loaded = false;

    if (this._activeRoute.params != null) {
      this._activeRoute.params.subscribe( (params: MarkSixResult) => {
        if (params != null) {
          this.result = params as MarkSixResult;
          this.loaded = true;
        } else {
          this.loaded = false;
        }

        if (this.result.drawNo == null || this.result.number1 == null || this.result.number2 == null || this.result.number3 == null
          || this.result.number4 == null || this.result.number5 == null || this.result.number6 == null || this.result.numberX == null) {
          this.loaded = false;
          // this._router.navigate(['']);
        }
      });
    } else {
      this.loaded = true;
    }
  }

  ngOnDestroy() {
  }


  toDate(val) {
    if (val != null) {
      return this.m6service.toDateStr(Number(val));
    } else {
      return '';
    }
  }

}
